.A_Button {
    all: unset;
    box-sizing: border-box;
    padding: 10px 20px;
    font-size: 16px;
    cursor: pointer;
    border: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
    width: 334px;
    height: 48px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 583px;
    font-family: Manrope;
    font-size: 16px;
    font-style: normal;
    font-weight: 600;
    line-height: normal;
    transition: all 0.1s ease-in-out;
  }
  
  .primary {
    background-color: #fff;
    color: #181818;
  }
  
  .secondary {
    background-color: #181818;
    box-shadow: 0px 0px 12px 0px rgba(255, 255, 255, 0.25) inset;
    color: white;
  }
  
  .disabled {
    background-color: #d6d6d6;
    color: #a0a0a0;
    cursor: not-allowed;
  }
  
  .primary:not(.disabled):hover {
    box-shadow: 0px 0px 14px 0px rgba(255, 255, 255, 0.55);
}
  
  .secondary:not(.disabled):hover {
    background-color: #5a6268;
  }  